.comments-input, .comments-textarea { width: 100%; padding: 10px; margin: 8px 0; border-radius: 8px; border: 1px solid #ccc; }
.comments-add-btn { width: 100%; padding: 10px; border: none; background: #007bff; color: white; border-radius: 6px; cursor: pointer; }
.comments-list { list-style: none; padding: 0; }
.comments-item { display: flex; justify-content: space-between; background: #f7f7f7; padding: 10px; margin-bottom: 8px; border-radius: 8px; }
.comments-story { font-weight: bold; }
.comments-text { margin-top: 4px; color: #444; }
